import HighCharts from 'highcharts'

export default {
  data() {
    return {
      isNodata: false,
      option: {
        chart: {
          plotBackgroundColor: null,
          plotBorderWidth: null,
          plotShadow: false //指定图表的类型，默认是折线图（line）
        },
        title: {
          text: ''
        },
        credits: {
            enabled: false
        },
        tooltip: {
          headerFormat: '{point.name.substr(0,10)}<br>',
          pointFormat: '所占比例: <b>{point.percentage:.1f}%</b>'
        },

        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: 'pointer',
            showInLegend: true,
            dataLabels: {
              enabled: false,
              format: '{point.percentage:.1f} %'
            },
            point: {
              events: {
                legendItemClick: function(e) {
                  return false // 直接 return false 即可禁用图例点击事件
                }
              }
            }
          }
        },
        colors: [
          '#f0798f',
          '#df76cb',
          '#7072e9',
          '#9972e7',
          '#f0798f',
          '#df76cb',
          '#7072e9',
          '#9972e7',
          '#238BD4',
          '#A0D677',
          '#FFF075',
          '#FCC652',
          '#74BFE7',
          '#E27561',
          '#338FDE',
          '#37ADE5',
          '#A6D3E4',
          '#3B7AAE'
        ],
        legend:{
            itemStyle:{
                fontSize: '1.3rem',
                color:'#333333',
                fontWeight:"normal"
            }
        },
        series: [
          {
            type: 'pie',
            data: this.quesOptiondata
          }
        ]
      }
    }
  },
  props: [
    'quesId',
    'quesTitle',
    'quesOption',
    'quesOptiondata',
    'quesType',
    'quesIndex'
  ],
  mounted() {
    var numSum = 0
    for(var i in this.quesOptiondata) {
      numSum+= this.quesOptiondata[i][1]
    }
    if(numSum == 0){
      this.isNodata = true
    }else{
      this.isNodata = false
      var quesId = this.quesId.toString()
      HighCharts.chart(quesId, this.option)
    }
  }
  //
}



