import HighCharts from 'highcharts'

export default {
  data() {
    return {
      isNodata: false,
      option: {
        chart: {
          type: 'column'
        },
        credits: {
          enabled: false
        }, //去掉地址
        title: {
          text: '' //指定图表标题
        },
        colors: [
          '#f0798f',
          '#df76cb',
          '#7072e9',
          '#9972e7',
          '#f0798f',
          '#df76cb',
          '#7072e9',
          '#9972e7',
          '#238BD4',
          '#A0D677',
          '#FFF075',
          '#FCC652',
          '#74BFE7',
          '#E27561',
          '#338FDE',
          '#37ADE5',
          '#A6D3E4',
          '#3B7AAE'
        ],
        xAxis: {
          categories: this.quesOption, //指定x轴分组
          labels: {
            rotation: 0,
            style: {
              fontSize: '1.3rem'
            }
          }
        },
        yAxis: {
          title: {
            text: ' ' //指定y轴的标题
          },
          labels: {
              style: {
                  fontSize: '1.3rem',
                  color:'#cccccc'
              }
          }
        },
        plotOptions: {
          column: {
            colorByPoint: true
          }
        },
        tooltip: {
            formatter: function () {
              var aaa = this.x.substr(0,10)+"..."+'<br/>人数:<b>'+this.y+'</b>'
              return aaa
            }
        },
        series: [
          {
            type: 'column',
            colorByPoint: true,
            data: this.quesOptiondata,
            showInLegend: false
          }
        ]
      }
    }
  },
  props: [
    'quesId',
    'quesTitle',
    'quesOption',
    'quesOptiondata',
    'quesType',
    'quesIndex'
  ],
  mounted() {
    var numSum = 0
    for(var i in this.quesOptiondata) {
      numSum += this.quesOptiondata[i]
    }
    if(numSum == 0){
      this.isNodata = true
    }else{
      this.isNodata = false
      var quesId = this.quesId.toString()
      HighCharts.chart(quesId, this.option)
    }
  }
}



