
import Api from '@/js/common'
import XTable from 'vux/src/components/x-table/index.vue'


export default {
data() {
return {
list:[],
isNodata:false,
total:0
}
},
props: ['quesId','quesTitle','quesText','quesType','quesIndex'],
methods: {
getList(){
var vmSelf = this
var text = vmSelf.quesText
text = JSON.parse(text)
if(!text[0].texts){
vmSelf.isNodata = true
}else{
vmSelf.list = text[0].texts
vmSelf.total = text[0].texts.length
vmSelf.isNodata = false
}
},
changeTimeType(time){
return Api.changeTimeType(time)
}
},
created(){
this.getList()
},
components: {
XTable
}
}



