
import headTop from "@/components/header";
import ViewBox from 'vux/src/components/view-box/index.vue'

import { mapGetters, mapMutations } from "vuex";
import Api from "@/js/common";
import chartPie from "@/pages/investigate/charts/chartPie";
import tableFile from "@/pages/investigate/charts/tableFile";
import chartLine from "@/pages/investigate/charts/chartLine";
import chartCircle from "@/pages/investigate/charts/chartCircle";

export default {
data() {
return {
charts: "",
title: "",
descr: "",
pushNumber: "",
replyNumber: "",
create_date: "",
expire_date: "",
num: "",
ques_bank: [],
questionList: []
};
},
computed: {
...mapGetters(["device"])
},
methods: {
getList() {
var vmSelf = this;
vmSelf.$vux.loading.show({
text: "Loading"
});
var submitObj = {
ques_id: vmSelf.$route.params.id,
ques_wj: vmSelf.$route.params.quesWj
};
Api.questionForm(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide();
var data = response.data;
vmSelf.title = data.ques.title;
vmSelf.descr = data.ques.descr;
vmSelf.pushNumber = data.ques.po_num;
vmSelf.replyNumber = data.ques.re_num;
vmSelf.create_date = data.ques.create_date;
vmSelf.expire_date = data.ques.expire_date
? data.ques.expire_date
: "";
vmSelf.num = data.ques.num;
vmSelf.ques_bank = data.ques_bank;
vmSelf.forInList();
})
.catch(function(err) {
console.log("=====", err);
});
},
forInList() {
var forInfo = this.ques_bank;
for (var i in forInfo) {
if (forInfo[i].type == 4 || forInfo[i].type == 5) {
var info = {
id: forInfo[i].id,
title: forInfo[i].name,
text: forInfo[i].options,
type: forInfo[i].type
};
} else if (forInfo[i].type == 1) {
var option = [];
var optionData = [];
var infoList = JSON.parse(forInfo[i].options);
for (var j in infoList) {
option.push(infoList[j].text);
var count = infoList[j].count ? infoList[j].count : 0;
optionData.push(count);
}
var info = {
id: forInfo[i].id,
title: forInfo[i].name,
option: option,
optionData: optionData,
type: forInfo[i].type
};
} else {
var option = [];
var optionData = [];
var infoList = JSON.parse(forInfo[i].options);
for (var j in infoList) {
option.push(infoList[j].text);
var data = [
infoList[j].text,
infoList[j].count ? infoList[j].count : 0
];
optionData.push(data);
}
var info = {
id: forInfo[i].id,
title: forInfo[i].name,
option: option,
optionData: optionData,
type: forInfo[i].type
};
}
this.questionList.push(info);
}
},
changeTime(time) {
time = Api.changeTimeType(time);
return time;
}
},
components: {
ViewBox,
headTop,
chartPie,
tableFile,
chartLine,
chartCircle
},
mounted() {
this.getList();
appcan.setNavigationBarTitle({
title: "调查问卷"
});
}
};



